'use strict';
document.addEventListener("DOMContentLoaded", () => { 
    //tabs
    const tabButtons = document.querySelectorAll('.tabheader__item'),
        tabContents = document.querySelectorAll('.tabcontent'),
        tabhWrapper = document.querySelector('.tabheader__items');
    
    
    tabhWrapper.addEventListener('click', (event) => { 
        if (event.target && event.target.classList.contains("tabheader__item")) {
        
            tabButtons.forEach((btn, i) => {
                btn.addEventListener('click', () => {
                    hideContent();
                    showContent(i);
                });
             

         });
        
        }
    });
    
    function hideContent() {
        tabContents.forEach((content) => {
            content.style.display = "none";

        });

        tabButtons.forEach((btn) => {
            btn.classList.remove("tabheader__item_active");

         });

    }
    
    function showContent(i) {
         tabContents[i].style.display = "block";

         tabButtons[i].classList.add("tabheader__item_active");

        
    }

    hideContent();
    
    showContent(0);

    //timer

    const deadline = '2022-07-11';
    
    function getTimeRemaining(endTime) {
        let days, hours, minutes, seconds;
        const totalTime = Date.parse(endTime) - Date.parse(new Date());

        if (totalTime <= 0) {
            days = 0;
            hours = 0;
            minutes = 0;
            seconds = 0;          
           
        } else {
            days = Math.floor(totalTime / (24 * 60 * 60 * 1000));
                hours = Math.floor((totalTime / (60 * 60 * 1000)) % 24);
                minutes = Math.floor((totalTime / (60 * 1000)) % 60);
            seconds = Math.floor((totalTime / 1000) % 60); 
        }


        return {totalTime, days, hours, minutes, seconds};
    } 



    function getZero(num) {
        if (num >= 0 && num < 10) {
            return `0${num}`;
        } else {
            return `${num}`;
        }
    }


    function setTime(selector, endTime) {
        const timer = document.querySelector(selector),
            days = timer.querySelector("#days"),
            hours = timer.querySelector("#hours"),
            minutes = timer.querySelector("#minutes"),
            seconds = timer.querySelector("#seconds"),
            timeInterval = setInterval(updateTime, 1000);
        
        updateTime();
        
        function updateTime() {
            const t = getTimeRemaining(endTime);

            days.innerHTML = getZero(t.days);
            hours.innerHTML = getZero(t.hours);
            minutes.innerHTML = getZero(t.minutes);
            seconds.innerHTML = getZero(t.seconds);

            if (t.totalTime <= 0) {
                clearInterval(timeInterval);
                timer.insertAdjacentHTML('beforeend', '<div class="timer__alert">Время акции истекло!!</div>');
            }
        }
        
    }

    setTime('.timer', deadline);



    //modal



    const modalButtons = document.querySelectorAll('button[data-modal]'),
        modalClose = document.querySelector('div[data-close]'),
        modal = document.querySelector('.modal');
    
    
    
    modalButtons.forEach((btn) => { 
        btn.addEventListener('click', () => { 
            showModal();


        });
    });

    modalClose.addEventListener('click', () => { 
        closeModal();
        });
        
    modal.addEventListener('click', (event) => {
        if (event.target === modal) {
            closeModal();
        }
    });

    function showModal() {
        modal.classList.add('modal__show');
        modal.classList.remove('modal__hide');
        document.body.style.overflow = 'hidden';
        clearInterval(modalTimerId);
    }


    function closeModal() {
        modal.classList.add('modal__hide');
        modal.classList.remove('modal__show');
        document.body.style.overflow = '';
    }


    document.addEventListener('keydown', (event) => {
        if (event.code === "Escape" && modal.classList.contains('modal__show')) {
             closeModal();
        }
    });
    

    
    const modalTimerId = setTimeout(showModal, 8000);

    function showModalByScroll() {
        if (window.pageYOffset + document.documentElement.clientHeight >= document.documentElement.scrollHeight - 1) {
            showModal();
            window.removeEventListener('scroll', showModalByScroll);
        }
    }
    window.addEventListener('scroll', showModalByScroll);





// classes

    class menuItem{
        constructor(src, alt, subtitle, description, price, parentSelector, ...classes) {
            this.src = src;
            this.alt = alt;
            this.subtitle = subtitle;
            this.description = description;
            this.price = price;
            this.transfer = 28;
            this.parentSelector = document.querySelector(parentSelector);
            this.classes = classes;
        }

        transferToUAN() {
            this.price = this.price * this.transfer;
            return this.price;
        }


        createElement() {
            const element = document.createElement("div");
            if (this.classes.length === 0) {
                this.element = "menu__item";
                element.classList.add(this.element);
            } else {
                this.classes.forEach((className) => element.classList.add(className));
            } 
            element.innerHTML = `
                <img src=${this.src} alt=${this.alt}>
                <h3 class="menu__item-subtitle">${this.subtitle}</h3>
                <div class="menu__item-descr">${this.description}</div>
                <div class="menu__item-divider"></div>
                <div class="menu__item-price">
                <div class="menu__item-cost">Цена:</div>
                <div class="menu__item-total"><span>${this.transferToUAN()}</span> грн/день
            `;
            this.parentSelector.append(element);

        }
    }

    new menuItem("img/tabs/vegy.jpg",
        "vegy",
        'Меню "Фитнес"',
        'Меню "Фитнес" - это новый подход к приготовлению блюд: больше свежих овощей и фруктов. Продукт активных и здоровых людей. Это абсолютно новый продукт с оптимальной ценой и высоким качеством!',
        9,
        "div.menu__field>div.container",
        "menu__item"
        
    ).createElement();

    new menuItem( "img/tabs/post.jpg",
        "post",
        'Меню "Постное"',
        'Меню “Постное” - это тщательный подбор ингредиентов: полное отсутствие продуктов животного происхождения, молоко из миндаля, овса, кокоса или гречки, правильное количество белков за счет тофу и импортных вегетарианских стейков.',
        14,
        "div.menu__field>div.container",
        'menu__item'
    ).createElement();

    new menuItem(  "img/tabs/elite.jpg",
        "elite",
        'Меню “Премиум”',
        'В меню “Премиум” мы используем не только красивый дизайн упаковки, но и качественное исполнение блюд. Красная рыба, морепродукты, фрукты - ресторанное меню без похода в ресторан!',
        21,
        "div.menu__field>div.container",
        'menu__item'
    ).createElement();
    
});


 

